* {
  box-sizing: border-box;
}

body {
  color: #060b35;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
}

.header {
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  flex-shrink: 0;
  width: 40px;
  height: 40px;
  display: block;
}

.header__img {
  object-fit: contain;
  width: 100%;
  height: 100%;
  display: block;
}

.head-nav__list {
  flex-wrap: wrap;
  gap: 20px;
  margin: 0;
  padding: 0;
  display: flex;
}

.head-nav__item {
  list-style: none;
}

.head-nav__link {
  color: #060b35;
  text-transform: uppercase;
  min-height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: block;
  position: relative;
}

.head-nav__link.is-active, .head-nav__link:hover {
  color: #00acdc;
}

.head-nav__link.is-active:after {
  content: "";
  background: currentColor;
  border-radius: 8px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  display: block;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--0 .stars__star:not(:nth-child(n+1)), .stars--1 .stars__star:not(:nth-child(n+2)), .stars--2 .stars__star:not(:nth-child(n+3)), .stars--3 .stars__star:not(:nth-child(n+4)), .stars--4 .stars__star:not(:nth-child(n+5)), .stars--5 .stars__star:not(:nth-child(n+6)) {
  background-image: url("star-active.edda4c43.svg");
}

.card {
  border: 1px solid #f3f3f3;
  width: 100%;
  max-width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
}

.card__img {
  object-fit: cover;
  object-position: center;
  max-width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
}

.card__product-code {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__rate {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__stars {
  margin: 0;
  padding: 0;
}

.card__reviews {
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  line-height: 18px;
  display: flex;
}

.card__price-tag {
  color: #616070;
}

.card__price-value {
  font-size: 16px;
  font-weight: 700;
}

.card__link {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  transition: background-color .3s, color .3s;
  display: block;
}

.card__link:hover {
  color: #00acdc;
  background: #fff;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.catalog {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .catalog {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .catalog {
    --column-count: 4;
  }
}

